.search-icon {
  color: "#9aa0a6";
  font-size: 15px;
  margin-top: 14px;
  margin-left: 15px;
}
.empty-icon {
  color: #9aa0a6;
  font-size: 15px;
  margin-top: 14px;
  margin-right: 15px;
  cursor: pointer;
}
.input {
  outline: none;
  color: rgba(0, 0, 0, 0.87);
  word-wrap: break-word;
  flex-grow: 1;
  margin-left: 10px;
  border-width: 0;
}
.container {
  width: "100%";
  height: 44px;
  background-color: #fff;
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  border-color: rgba(223, 225, 229, 0);
  border-radius: 24px;
  background: #fff;
  display: flex;
  border: 1px solid #dfe1e5;
  flex-direction: row;
  flex: 1;
  overflow: hidden;
}
